

































































































































































































































































import { Component, Vue } from "vue-property-decorator";
import DataService from "../services/DataService";

@Component
export default class Match extends Vue {
  private year = 0;
  private selectableYears: number[] = [];
  private match: any = {
    oldGoals: null,
    youngGoals: null,
  };
  private tableaux: any = [
    {
      rank: null,
      name: null,
      scoredGoals: 0,
      goalDifference: 0,
      points: 0,
    },
  ];
  private lastMatches: any = [
    {
      homeTeam: null,
      awayTeam: null,
      homeGoals: null,
      awayGoals: null,
      date: null,
    }
  ];
  private tournamentTableaux: any = [
    {
      rank: null,
      name: null,
      scoredGoals: 0,
      goalDifference: 0,
      points: 0,
    },
  ];
  private lastTournamentMatches: any = [
    {
      homeTeam: null,
      awayTeam: null,
      homeGoals: null,
      awayGoals: null,
      date: null,
    }
  ];
  private tournament: any = [
    {homeTeam: "Old", awayTeam: "Young", homeGoals: null, awayGoals: null},
    {homeTeam: "Middle", awayTeam: "Young", homeGoals: null, awayGoals: null},
    {homeTeam: "Middle", awayTeam: "Old", homeGoals: null, awayGoals: null},
    {homeTeam: "Young", awayTeam: "Old", homeGoals: null, awayGoals: null},
    {homeTeam: "Young", awayTeam: "Middle", homeGoals: null, awayGoals: null},
    {homeTeam: "Old", awayTeam: "Middle", homeGoals: null, awayGoals: null},
  ];

  async saveMatch() {
    if(confirm("Ergebnis " + this.match.oldGoals + ":" + this.match.youngGoals + " eintragen?")) {
      let data = {
        oldGoals: Number.parseInt(this.match.oldGoals),
        youngGoals: Number.parseInt(this.match.youngGoals),
      };
      await DataService.saveMatch(data);
      this.updateTableaux();
      this.updateLastMatches();
    }
  }

  async saveTournament() {
    if(confirm("Turnier-Ergebnis eintragen?")) {
      let data = [];
      let i = 0;
      for (const row of this.tournament) {
        data[i] = row;
        data[i].homeGoals = Number.parseInt(row.homeGoals);
        data[i].awayGoals = Number.parseInt(row.awayGoals);
        i++;
      }
      await DataService.saveTournament(data);
      this.updateTournamentTableaux();
      this.updateLastTournamentMatches();
    }
  }

  async updateTableaux() {
    try {
      this.tableaux = (await DataService.getTableaux(false, this.year)).data;
    } catch (error) {
      console.error(error);
    }
  }

  async updateTournamentTableaux() {
    try {
      this.tournamentTableaux = (await DataService.getTableaux(true, this.year)).data;
    } catch (error) {
      console.error(error);
    }
  }

  async updateLastMatches() {
    try {
      this.lastMatches = (await DataService.getMatches(false, 5)).data;
    } catch (error) {
      console.error(error);
    }
  }

  async updateLastTournamentMatches() {
    try {
      this.lastTournamentMatches = (await DataService.getMatches(true, 6)).data;
    } catch (error) {
      console.error(error);
    }
  }

  setCurrentYear(current: number) {
    this.year = current;
    this.updateTableaux();
    this.updateTournamentTableaux();
  }


  mounted() {
    for (let year = new Date().getFullYear()-1; year >= 2017; year--) {
      this.selectableYears.push(year);
    }
    this.updateTableaux();
    this.updateTournamentTableaux();
    this.updateLastMatches();
    this.updateLastTournamentMatches();
  }
}
